@font-face {
  font-family: "Open Sans Regular";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans Semibold";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans Bold";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  font: 400 18px/1.4 "Open Sans Regular", sans-serif;
  color: #959595;
  background-color: #d50000;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media only screen and (max-width: 900px) {
  body {
    font-size: 16px;
  }
}
@media only screen and (max-width: 700px) {
  body {
    background: #fff;
  }
}
body.is-hidden {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #fff;
}

.btn,
.btn-default {
  font: 400 20px/1.4 "Open Sans Bold", sans-serif;
  text-align: center;
  border-radius: 30px;
  display: inline-block;
  background-color: #d50000;
  padding: 7px 59px;
  transition: background-color 0.25s;
}
@media only screen and (max-width: 900px) {
  .btn,
.btn-default {
    font-size: 16px;
    padding: 7px 30px;
  }
}
.btn:hover,
.btn-default:hover {
  background-color: #ef0000;
}
.btn:active,
.btn-default:active {
  transform: translateY(1px);
}

.btn_position {
  position: relative;
  z-index: 1;
}

.btn-default {
  color: #fff;
}

.btn-scroll {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  padding: 10px;
  display: inline-block;
}
.btn-scroll_position {
  position: relative;
  z-index: 1;
}
.btn-scroll__ico {
  width: 20px;
  padding: 20px 0 0;
  background: url(../img/arrow-bottom.svg) no-repeat center;
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
  transition: transform 0.25s;
}
.btn-scroll:hover .btn-scroll__ico {
  transform: translateY(5px);
}

.btn-more {
  text-align: center;
  font: 400 16px/1.4 "Open Sans Bold", sans-serif;
  color: #d50000;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  position: relative;
}
.btn-more:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  margin: -2px 0 0;
  height: 1px;
  background-color: #d50000;
  transition: opacity 0.25s, color 0.25s;
  opacity: 1;
}
.btn-more:hover:after {
  opacity: 0;
}
.btn-more:active {
  top: 1px;
}

.btn-close {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  cursor: pointer;
  background: none;
  position: absolute;
  top: 28px;
  right: 28px;
  padding: 10px;
}
@media only screen and (max-width: 900px) {
  .btn-close {
    top: 10px;
    right: 10px;
  }
}
.btn-close .icon {
  transition: fill 0.4s;
  font-size: 32px;
}
@media only screen and (max-width: 700px) {
  .btn-close .icon {
    font-size: 26px;
  }
}
.no-touch .btn-close:hover .icon {
  fill: #d7d7d7;
}

.title {
  font: 400 46px/1.2 "Open Sans Regular", sans-serif;
  color: #fff;
  position: relative;
  z-index: 1;
}
@media only screen and (max-width: 900px) {
  .title {
    font-size: 32px;
  }
}
@media only screen and (max-width: 700px) {
  .title {
    font-size: 26px;
  }
}

.input {
  display: block;
  width: 100%;
  background-color: #fff;
  border: 2px solid #bdbdbd;
  border-radius: 50px;
  color: #bdbdbd;
  font: 400 20px/1.2 "Open Sans Regular", sans-serif;
  padding: 16px 30px;
  transition: border-color 0.25s, color 0.25s;
}
@media only screen and (max-width: 900px) {
  .input {
    font-size: 16px;
    padding: 10px 20px;
